<template>
  <div class="page">
    <div class="cont" id="shareCont">
      <div class="top-tit">{{ title }}</div>
      <div class="shop-icon">
        <img src="/static/img/collection/img_icon4@2x.png" />
      </div>
      <div class="shop-code">XXF1001#0001/9999</div>
      <div class="common-txt group1">
        <div class="label">持有者</div>
        <div class="text">张*</div>
        <div class="label">认证时间</div>
        <div class="text">2022-03-01 12:00:00</div>
      </div>
      <div class="hash">交易哈希</div>
      <div class="hash-cont">
        jfajahi473856hfhaihfkahair7e89qrhfajkfnfHFaufaia7837475
      </div>
      <div class="tips">
        <div class="tips-left">
          <div>中信证券元宇宙实验室·信纪元</div>
          <div>扫码参与探索更多数字藏品</div>
        </div>
        <div class="qrcode">
          <img src="~@/assets/images/collection/qrcode@2x.png" />
        </div>
      </div>
    </div>
    <div class="btn-box">
      <div class="btn">
        <img
          @click="share"
          src="@/assets/images/personalPage/btn_go@2x.png"
          alt=""
        />
      </div>
      <div class="copyright">恒生·范太链提供技术支持</div>
    </div>
    <share :visible="showShare" @hidePop="hideShare"></share>
  </div>
</template>

<script>
// import html2canvas from 'html2canvas'
export default {
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      title: '小雪·信小福',
      showShare: false
    }
  },
  methods: {
    share () {
      this.showShare = true
      // let that = this
      // html2canvas(document.getElementById('shareCont')).then(function (canvas) {
      //   let imgurl = canvas.toDataURL('image/jpg')
      //   let a = document.createElement('a') // 新建a标签
      //   a.href = imgurl // 修改属性，url是一段base64
      //   a.download = that.title + '.jpg'
      //   a.click()
      // })
    },
    hideShare () {
      this.showShare = false
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.page {
  position: relative;
  height: 100%;
  .bg-image("~@/assets/images/collection/share_bg","png");
  .flex_func(column, flex-satrt,center,nowrap);
  .cont {
    width: 654px;
    min-height: 1012px;
    margin-top: 15px;
    padding: 30px 54px 60px;
    .bg-image("~@/assets/images/collection/share_card","png");
    .flex_func(column,center,center,nowrap);
    .top-tit {
      height: 50px;
      line-height: 50px;
      font-size: 36px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #472e18;
      line-height: 50px;
    }
    .shop-icon {
      width: 390px;
      height: 390px;
      > img {
        width: 100%;
      }
    }
    .shop-code {
      // width: 100%;
      font-size: 30px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #472e18;
      line-height: 42px;
    }
    .common-txt {
      width: 100%;
      text-align: left;
    }
    .group1 {
      font-size: 28px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #472e18;
      line-height: 50px;
      margin-top: 35px;
      .flex_func(row,flex-start,center,wrap);
      .label {
        width: 150px;
      }
      .text {
        width: calc(100% - 150px);
      }
    }
    .hash {
      margin: 30px 0;
      font-size: 24px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #c3a68e;
      line-height: 34px;
    }
    .hash-cont {
      width: 100%;
      min-height: 106px;
      padding: 15px 25px;
      background: #ffffff;
      border-radius: 12px;
      font-size: 26px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #c3a68e;
      flex-wrap: wrap;
      line-height: 30px;
      white-space: break-spaces;
      word-break: break-word;
    }
    .tips {
      width: 100%;
      min-height: 120px;
      .flex_func(row,space-between,center,nowrap);
      font-size: 26px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #472e18;
      line-height: 36px;
      .tips-left {
        > div {
          width: 100%;
          line-height: 45px;
        }
      }
      .qrcode {
        width: 120px;
        height: 120px;
        > img {
          width: 100%;
        }
      }
    }
  }
  .btn-box {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    .flex_func(column,center,center,nowrap);
    .btn {
      img {
        width: 396px;
        height: 112px;
      }
    }
    .copyright {
      width: 100%;
      text-align: center;
      font-size: 24px;
      color: #9d2e10;
      line-height: 33px;
      margin: 20px 0 40px;
      height: 40px;
    }
  }
}
</style>



